import React, { Component } from 'react';

export default class ListItem extends Component {
  constructor() {
    super();

    this.state = {
      completed: false
    };

    this.onLabelCkick = () => {
      this.setState(prevState => {
        return {
          completed: !this.state.completed
        };
      });
    };
  }

  render() {
    const { label, onDeleted, onChanged } = this.props;

    return (
      <div
        className='view'
        onClick={this.onLabelCkick}
        onDoubleClick={onChanged}
      >
        <input
          className='toggle'
          type='checkbox'
          checked={this.state.completed}
          onChange={this.onLabelCkick}
        />
        <label className={this.state.completed ? 'active' : null}>
          {label}
        </label>
        <button className='destroy' onClick={onDeleted} />
      </div>
    );
  }
}
