import React, { Component } from 'react';
import Header from './components/Header';
import Main from './components/Main';
import Footer from './components/Footer';

class App extends Component {
  constructor() {
    super();
    let id = Date.now();
    this.state = {
      todoData: []
    };

    function createToDo(label) {
      return {
        label,
        id: id++
      };
    }

    this.addItem = str => {
      const newItem = createToDo(str);

      this.setState(({ todoData }) => {
        const newArr = [...todoData, newItem];

        return {
          todoData: newArr
        };
      });
    };

    this.deleteItem = id => {
      this.setState(({ todoData }) => {
        const index = todoData.findIndex(el => el.id === id);
        todoData.splice(index, 1);
        const before = todoData.slice(0, index);
        const after = todoData.slice(index);
        const newTodo = [...before, ...after];
        return {
          todoData: newTodo
        };
      });
    };

    this.changeItem = id => {
      this.setState(({ todoData }) => {
        const index = todoData.findIndex(el => el.id === id);
        console.log(index);
      });
    };
  }

  render() {
    return (
      <section className='todoapp'>
        <Header onItemAdded={this.addItem} />

        <Main
          todos={this.state.todoData}
          onDeleted={this.deleteItem}
          onChanged={this.changeItem}
        />
        {this.state.todoData.length ? (
          <Footer todoData={this.state.todoData.length} />
        ) : null}
      </section>
    );
  }
}

export default App;
